.resultado {
  display: block;
}

.container2 {
  max-width: 1200px;
  margin: 0 auto;
}

.resultado h2 {
  font-size: 2rem;
  margin: 0;
  padding-top: 15px;
}

.resultadoNumero {
  display: flex;
  gap: 20px;
}

.resultado p {
  border: 1px solid;
  padding: 5px 10px;
  border-radius: 50%;
  box-sizing: border-box;
  font-size: 1.5rem;
  color: white;
}
